/* home.css */

.HS1 {
  padding-left: 5px;
}

.HS2 {
  float: right;
  padding-right: 5px;
  cursor: pointer;
  user-select: none;
}

.HS3 {
  float: right;
  height: 16px;
  margin-top: 12px;
  cursor: pointer;
  user-select: none;
}

.HS4 {
  float: right;
  padding-right: 10px;
}

.HS5 {
  padding-left: 530px;
  color: #1301d5;
  font-weight: bold;
  font-style: Georgia;
  font-size: large;
  user-select: none;
}

.HS6 {
  padding-left: 5px;
  float: left;
  height: 40px;
  margin-top: 1px;
  cursor: pointer;
  user-select: none;
}

.HS7 {
  padding-left: 5px;
  color: #ff0303;
  font-weight: bold;
  font-style: italic;
  font-size: large;
  user-select: none;
}
.HS8 {
  color: rgb(0, 0, 0);
  font-size: x-large;
  padding: 25%;
}
.HS9 {
  float: right;
  height: 25px;
  width: 25px; /* Set width same as height to make it a circle */
  margin-top: 7px;
  margin-right: 5px; 
  user-select: none;
  border-radius: 50%; /* Make it a circle */
  border: 2px solid black; /* Define border */
}



.firstheader {
    background-color: #e3f7fa;
    height: 40px;
    color: #333333;
    line-height: 40px;
    font-weight: bold;
    font-size: 12pt;
  }


.secondheader {
    background-color: #01bcd5;
    height: 40px;
    color: #333333;
    line-height: 40px;
    font-weight: bold;
    font-size: 12pt;
  }


.content {
    width:100%;
    height: calc(100% - 110px);
    background: white;
  }


  .footer {
    background-color: #01bcd5;
    height: 30px;
    color: black;
    line-height: 30px;
    font-size: 8pt;
    font-weight: bold;
    text-align: center;
  }


  .menubar{
    width: 246px;
    padding: 2px;
    height: calc(100% - 4px);
    background: #e3f7fa;
    float: left;
}

.outlet{
    width: calc(100% - 250px);
    height: 100%;
    float: left;
}

.menuheader{

  width:100%;
  height: 30px;
  line-height: 30px;
  background:  #1E2B32
}

.menuheader img{

  height:16px;
  float:left;
  padding-left: 5px;
  margin-top: 6px;  
  cursor: pointer;  
}

.menuheader label{

  color: white;
  font-weight: bold;
  float:left;
  padding-left: 10px;
  cursor: pointer;  
}

.menu
{
    width: 100%;
    height: calc(100% - 30px);
    overflow: auto;
    background-color: rgb(232, 248, 248);
}



.mlist
{
    width: 100%;
    list-style-type: none;
    margin : 0;
    padding : 0;
}

.mlist li label{
    display : block;
    padding : 10px 5px;
    cursor: pointer;
    background:  #1E2B32;
    border-top : 2px solid white;
    color: white;
    
}

.mlist li label:hover{
    background:  #006cc4;

}


.mlist li div{
  display : block;
    width: 100%;

}

.mlist li div label{
    display : block;
    padding : 8px 5px;
    cursor: pointer;
    background: #8782829f;
    border-top : 2px solid white;

}

.smenu {
  display: none;
  transition: max-height 0.5s ease; /* Define transition property */
  overflow: hidden;
}

/* Add this if you want to transition the opacity as well */
.smenu.active {
  opacity: 1;
}


.outlet #titlebar{
  width: 100%;
  height: 30px;
  line-height: 30px;
  font-size: 10pt;
  font-weight: bold;
  background: whitesmoke;
}

.outlet #module{
  width: 100%;
  height: calc(100% - 30px);
  border: none;
}

.selected{
  background: blanchedalmond;
}


